<template>
    <div @click="showData = true" @mouseover="showData = true" @mouseleave="showData = false"
        class="project_card"
    >
        <div class="project_card-image">
            <img :src=getProjectCover() :alt=project.coverAlt >
            <span class="project_card-style" >
                {{ project.style }}        
            </span>
            <transition>
                <span class="project_card-listen" v-show="!showData">
                    Cliquez pour écouter
                </span>
            </transition>
        </div>

        <Transition>
            <div class="project_card-infos" v-show="showData" :class="{'fadeOut': !showData, 'fadeIn': showData }">
                
                <div class="project_card-infos_project">
                    <span class="project_card-infos_project--band bold">
                        {{ project.band }}
                    </span>
                    <span class="project_card-infos_project--album">
                        {{ project.album }}
                    </span>
                    <span class="project_card-infos_project--year">
                        {{ project.year }}
                    </span>
                    <!-- <span class="project_card-infos_project--style">
                        {{ style }}
                    </span> -->
                </div>
                <div class="project_card-infos_work">
                    <span v-for="work of project.works" :key = "work" class="project_card-infos_work--item"
                    :class="{
                        'rec': work == 'Rec',
                        'reamping': work == 'Reamping',
                        'mix': work == 'Mix',
                        'master': work == 'Master'
                        }">
                        {{ work }}
                    </span>
                </div>

            </div>  
        </Transition>

    </div>
    
</template>

<script>
export default {
  name: 'ProjectCard',
  components: {
  },
  props: [
    'project'
  ],
  data() {
    return {
        showData: false
    }
  },
  methods: {
    getProjectCover() {
        return require("@/assets/images/projects/" + this.project.cover)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.project_card {
    cursor: pointer;
    width: 250px;
    height: 250px;
    box-shadow: 5px 5px 20px black;
    border-radius: 0.6em;
    overflow: hidden;
    background: #000000;

    transform: scale(1);
    
    &-image {
        & img {
            width: 100%;
        }
    }

    &-style {
        display: inline-block;
        width: 100%;
        height: 20px;
        font-size: 0.8em;
        position: absolute;
        top: 0;
        left: 0px;
        background: rgba(#000000, 0.8);
        opacity: 0.7;
        align-content: center;
    }

    &-listen {
        @media (max-width: 1280px) {
            display: none;
        };
        display: inline-block;
        width: 100%;
        height: 20px;
        font-size: 0.8em;
        position: absolute;
        bottom: 0;
        left: 0px;
        background: rgba(#000000, 0.8);
        opacity: 0.7;
        align-content: center;
    }

    &-infos {
        color: white;
        user-select: none;
        width: 100%;
        height: 45%;
        display: flex;
        flex-direction: column; 
        justify-content: space-between;
        position: absolute;
        bottom: 0;
        left: 0px;
        background: rgba(#000000, 0.9);


        &_project {
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;

            &--band, &--album {
                height: 30%;
            }

            &--band {
                font-size: 1.1em;
            }

            &--album {
                font-size: 0.9em;
            }

            &--year {
                font-size: 0.8em;
            }

            &--style {
                font-size: 0.8em;
            }
        }

        &_work {
            height: 30px;
            transition: 800ms;
            font-size: 0.9em;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;

            &--item {
                width: fit-content;
                width: 80px;
                // padding: 0 10px;
                height: 18px;
                border-radius: 0.3em;   
            }
        }
    }
    
}

.rec {
    background: rgba(#525252, 0.33);
    box-shadow: 1px 1px 1px rgba(#525252, 0.33);
}

.reamping {
    background: rgba(#525252, 0.48);
    box-shadow: 1px 1px 1px rgba(#525252, 0.33);
}

.mix {
    background: rgba(#525252, 0.66);
    box-shadow: 1px 1px 1px rgba(#525252, 0.66);
}

.master {
    background: rgba(#525252, 1);
    box-shadow: 1px 1px 1px rgba(#525252, 1);
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}
</style>
