<template>
    <div class="sideplayer-container" v-show="showPlayer">
      <button class="sideplayer-container_close" @click="$emit('showCustomPlayer', false), pauseMusic()">X</button>
        <div class="sideplayer-container_player ">
        <div class="sideplayer-container_player_data">
            <div class="sideplayer-container_player_data-album">
            <img class="sideplayer-container_player_data-album--cover" :src=getProjectCover(project.cover) :alt=project.coverAlt>
            <span class="sideplayer-container_player_data-album--title">
                {{ project.album }}
            </span>
            </div>

            <div class="sideplayer-container_player_data-song">
            <span class="sideplayer-container_player_data-song--artist">{{ project.band }}</span>
            <span class="sideplayer-container_player_data-song--title">{{ project.songTitle }}</span>
            </div>
                    
            <div class="sideplayer-container_player_og-player">
                <audio v-show="project.musicFile" class="og-player" :src=getMusicPath(project.musicFile) controls autoplay controlsList=“nodownload,nofullscreen,noremoteplayback”></audio> 
            </div>
        </div>
        </div>
        <div class="sideplayer-container_links"> 
            <a class="sideplayer-container_links-item"
                v-for="(value, link) in project.links"
                :key=link
                :data-link=link
                :href=value target="_blank"
                v-show="value">
                <font-awesome-icon class="ppop-in_link-icon" v-show="link == 'youtube'" icon="fa-brands fa-youtube" />
                <font-awesome-icon class="ppop-in_link-icon" v-show="link == 'spotify'" icon="fa-brands fa-spotify" />
                <font-awesome-icon class="ppop-in_link-icon" v-show="link == 'bandcamp'" icon="fa-brands fa-bandcamp" />
                <font-awesome-icon class="ppop-in_link-icon" v-show="link == 'website'" icon="fa-slid fa-globe" />
                <font-awesome-icon class="ppop-in_link-icon" v-show="link == 'facebook'" icon="fa-brands fa-facebook" />
                <font-awesome-icon class="ppop-in_link-icon" v-show="link == 'instagram'" icon="fa-brands fa-instagram" />
                <div class="sideplayer-container_links-item--text">
                    {{ link }}
                </div>
            </a>   
          </div>   
        </div>
  </template>
  
  <script>
  export default {
    name: 'MusicPlayer',
    components: {
    },
    props: [
      'project',
      'showPlayer'
    ], 
    data() {
      return {
        musicPlaying: false
      }
    },
    mounted() {
    },
    methods: {
      getProjectCover(cover) {
          return require("@/assets/images/projects/" + cover)
      },
      getMusicPath(musicFile) {
        if(musicFile) {
            return require("@/assets/audio/" + musicFile)
        }
      },
      playMusic() {
        document.querySelector("audio").play();
        this.musicPlaying = true;
      },
      pauseMusic() {
        document.querySelector("audio").pause();
        this.musicPlaying = false;
      }
    }
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.sideplayer-container { 
    @media (max-width: 1280px) {
        display: none;
    };

    &-show {
        flex: 1;
        transition-duration: 200ms;
    }

    &-hide {
        transition-duration: 200ms;
    }

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: sticky;
    top: 120px;
  
  
      &_close {
        align-self: flex-end;
        margin: 10px 10px 20px 0;
        cursor: pointer;
        height: 35px;
        width: 35px;
        font-size: 0.9em;
        font-weight: bold;
        // padding: 0.5em;
        border-radius: 100%;
        background: rgba(255, 255, 255, 0.70);
        border: black 1px solid;
        box-shadow: 1px 1px 5px rgba(82, 80, 80, 0.699);
      }

      &_player {
        flex: 1;
        height: 500px;
        position: sticky;
        top: 0;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        &_data {
            &-album {
            border: black 3px solid;
            border-radius: 10px;
            overflow: hidden;
            width: 80%;
            margin: 0 auto;
            display: flex;
            flex-direction: column;
            background: #000000;

            &--cover {
                width: 100%;
                object-fit: cover;
                border-radius: 8px;
                overflow: hidden;
            }

            &--title {
                color: #ffffffb9;
                display: flex;
                justify-content: center;
            }
        }

        &-song {
        display: flex;
        flex-direction: column;
        padding-top: 28px;
        color: #E1E1E6;

        &--artist {
            font-size: 27px;
            color: #E1E1E6;
            padding-bottom: 7px;
            font-weight: bold;
        }

        &--title {
            opacity: 0.68;
            font-size: 19px;
            height: 40px;
        }
        }
    }

    &_og-player {
        align-items: center;
        display: flex;
        justify-content: center;
        height: 60px;
    }
      }
  
      &_links {
        height: 150px;
        width: 100%;
  
        &-item {
          color: white;
          padding: 10px;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          user-select: none;
          text-decoration: none;
          font-size: 1em;
  
          &:hover {
              background: rgba(#ffffff, 0.6);
              color: black;
              font-weight: bold;
              text-decoration: none;
          }
  
          &--text {
              font-size: 1em;
              width: 120px;
          }
        }
      }
}
  
.og-player {
background: transparent;
width: 80%;
}
</style>